<template>
  <router-link :to="path" class="nav-link mx-3" :class="{ active: isActive }">
    {{ name }}
  </router-link>
</template>

<script>
import { useRoute } from "vue-router";
export default {
  props: {
    name: String,
    path: String,
  },
  computed: {
    isActive() {
      return useRoute().path === this.path;
    },
  },
};
</script>

